* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  background-image: radial-gradient(#dfd7d7 5%, #0000 7%);
  background-repeat: repeat;
  background-size: 30px 30px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  min-width: 300px;
  max-width: 320px;
  margin: 50px;
  padding: 15px;
}

#search-box:hover, #search-box:focus, .search-box__input {
  border: 2px solid gray;
  outline: none;
}

.country-list, .country-info {
  margin: 0 50px;
  padding-left: 5px;
}

.name {
  flex-direction: row;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 15px;
  font-family: Cantarell, sans-serif;
  font-weight: 600;
  list-style: none;
  display: flex;
}

.country-list__item:focus, .country-list__item:hover {
  cursor: pointer;
  transform: scale(1.01);
}

.name__img {
  width: 2rem;
  margin-right: 15px;
}

.country-info__list {
  padding-left: 0;
  list-style: none;
}

.country-info__name {
  font-size: 40px;
}

.country-info__item {
  margin-bottom: 1rem;
}

.return-btn {
  width: 100px;
  height: 30px;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 50px;
  margin-bottom: 10px;
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.return-btn:focus, .return-btn:hover {
  color: #fff;
  cursor: pointer;
  background-color: gray;
  transform: scale(1.01);
}

